.welcome-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffff;
  overflow: hidden;
}

.welcome-title {
  position: absolute;
  font-family: "Bricolage Grotesque", sans-serif;
  top: 0;
  margin-top: 50px;
  transform: translateY(20px);
  animation: fadeInUp 1s forwards, slideUp 1s forwards;
}

@keyframes fadeInUp {
  from {
    transform: translateY(20px);
  }

  to {
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
  }

  to {
    transform: translateY(0);
  }
}

video {
  display: block;
  margin: 0 auto;
  max-width: 80%;
  max-height: 100%;
  object-fit: cover;
}

body::-webkit-scrollbar {
  width: 0;
}

.container-month-year {
  display: flex;
  align-items: center;

  border-radius: 15px;
}

#year-selected {
  font-family: "Kanit", sans-serif;
  font-size: 15px;
}

#month-selected {
  font-family: "Kanit", sans-serif;
  font-size: 15px;
}

.year-month {
  display: flex;
  width: fit-content;
}

.year-list {
  position: absolute;
  list-style: none;
  padding: 0;
  font-family: "Kanit", sans-serif;
  color: black;
  background-color: #d6dae5;
  cursor: pointer;
  margin-top: 20px;
  max-height: 200px;
  border-radius: 10px;
  z-index: 1;
  margin-right: 20;
  overflow-x: hidden;
  overflow-y: auto;
}

.month-list {
  position: absolute;
  font-family: "Kanit", sans-serif;
  padding: 0;
  list-style: none;
  background-color: #d6dae5;
  color: black;
  border-radius: 10px;
  cursor: pointer;
  max-height: 200px;
  margin-right: 20px;
  overflow-y: auto;
  overflow-x: hidden;
}

.month-list::-webkit-scrollbar {
  width: 3px;
}

.month-list::-webkit-scrollbar-thumb {
  background-color: darkslategrey;
}

.year-list::-webkit-scrollbar {
  width: 3px;
}

.year-list::-webkit-scrollbar-thumb {
  background-color: darkslategrey;
}

.year-value,
.month-value {
  text-align: center;
  text-decoration: underline solid lightseagreen;
  padding: 5px;
  /*border: 1px solid red;*/
}

/*.header {
  background-color: #333;
