  padding: 10px;
  text-align: right;
}*/
.how-many-ev-today {
  font-family: "Geologica", sans-serif;
  color: #080c16;
  font-size: 25px;
  font-weight: bolder;
  width: fit-content;
  margin: 0;
}

.hamburger {
  margin: 15px 18px;
  z-index: 2;
  width: fit-content;
  position: fixed;
  cursor: pointer;
  border-radius: 50%;
}

.date-string {
  width: fit-content;
}

.event {
  margin: auto;
}

.style-event-page {
  margin: auto;
}

.sub-content-event-page {
  box-sizing: border-box;
  padding: 20px;
  box-shadow: 0 0 1px 1px gray;
  /*backgroundColor : 'yellow';*/
  border-start-start-radius: 10px;
  border-start-end-radius: 10px;
  width: 100%;
}

@media (min-width: 1000px) {
  .container-month-year,
  .event,
  .event_day {
    cursor: pointer;
  }
}

@media (min-width: 480px) {
  .event-elem-style {
    margin: auto;
  }
}

@media (max-width: 800px) {
  .style-event-page {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .event-elem-style {
    margin-top: 15px;
    max-width: 100%;
  }

  .container-month-year {
    margin-top: 3px;
  }

  .event {
    margin-block: 8px;
    padding: 13px;
  }

  .year-list,
  .month-list {
    margin: 25px 25px 0 0;
  }
  .event-desc-style{
    max-width: 460px;
  }
}

@media (min-width: 500px) {
  .style-event-page {
    width: 500px;
  }

  .event-elem-style {
    width: 375px;
  }

  .event {
    margin-block: 8px;
    padding: 13px;
  }

  .next-back {
    margin-bottom: 60px;
  }
}

@media (max-width: 500px) {
  .year-month,
  .date-string,
  .how-many-ev-today {
    margin-right: auto;
    padding-inline-start: 17px;
  }

  .date-string {
    margin-block: 5px;
  }

  .next-back {
    margin-bottom: 50px;
  }
}

@media (min-width: 800px) {
  .style-event-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 800px;
    margin: 5% auto;
  }
  .event-desc-style{
    width: 760px;
  }

  .event-elem-style {
    margin-top: 15px;
  }

  .event {
    display: flex;
    flex-direction: column;
  }

  .year-month {
    margin-inline: 200px;
  }

  .year-list,
  .month-list {
    margin: 20px 25px 0 0;
  }

  .next-back {
    margin-bottom: 90px;
  }


}

@media (max-width: 300px) {
  .sub-content-event-page {
    padding: 15px 5px;
  }
}

@media (min-width: 370px) {
  .event {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

@media (min-width: 300px) {
  .event h2 {
    font-size: 25px;
  }

  .event div {
    margin-top: 10px;
  }

  .event div span {
    font-size: 12px;
    padding-inline-end: 15px;
  }
}

@media (max-width: 300px) {
  .event div span {
    font-size: 12px;
    padding-inline-end: 15px;
  }

  .event {
    display: flex;
    flex-direction: column;
    align-items: start;
  }

  .event h2 {
    font-size: 20px;
  }

  .event div {
    margin-top: 5px;
  }

  .event div span {
    font-size: 10px;
  }

  .next-back {
    margin-bottom: 22px;
  }
}

@media (min-width: 225px) {
  .next-back button {
    margin-inline: 15px;
    padding: 5px 20px;
    font-size: 15px;
  }
}

@media (max-width: 225px) {
  .next-back {
    margin-bottom: 0;
  }

  .next-back button {
    font-size: 15px;
    margin-inline: 5px;
  }
}

