.event-desc-style {
  /*align-items: start;*/
  position: absolute;
  z-index: 0;
  bottom: 0;
  bottom: 0;
  border-radius: 20px 20px 0 0;
  padding: 20px;
  height: fit-content;
  background-color: #fff;
  animation: showEventDesc 0.5s ease-in-out;
  box-shadow: 0px 0px 1px 1px lightgray;
}

@keyframes showEventDesc {
  0% {
    bottom: -100%;
  }

  100% {
    bottom: 0;
  }
}

@keyframes hideEventDesc {
  0% {
    bottom: 0;
  }

  100% {
    bottom: -100%;
  }
}

.event_desc {
  display: grid;
  grid-template: min-content min-content / repeat(2, 1fr);
  margin-block: 20px;

}

.event_desc div div {
  /*border : 1px solid black;*/
  display: flex;
  align-items: center;
}

.event_desc small {
  color: gray;
}

.next-back {
  position: absolute;
  bottom: 0;
  align-items: center;
}

.next-back button {
  font-family: "Geologica", sans-serif;
  color: #ffffff;
  font-weight: 600;
  border-radius: 8px;

  border: none;
}

.date-str-event-desc {
  font-family: "Comfortaa", cursive;
  font-weight: 400;
}
